import { Component, OnInit } from '@angular/core';
import { SettingsService } from '../../service/SettingsService';
import { UserService } from '../../service/user.service';
import { CommonService } from '../../service/commonService';
import {SettingsBean } from '../../typeBean/SettingsBean';
import { SocialLoginModule, AuthServiceConfig, GoogleLoginProvider,AuthService } from "angular5-social-login";
import { environment } from '../../../environments/environment';
import {ConfirmationBoxComponent} from '../../customComponents/confirmation-box/confirmation-box.component';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {

  public newUser = {companyId:"",firstname:"",lastname:"",email:"",password:"",phone:"",usertype:""};
  appUrl = "";
  companyId:string = "";
  public loaded:boolean = false;
  public companies = null;
  public allUser = [];

  public allUserIds = [];
  public availableUser = [];
  public selectedCompanyUser = [];
  constructor(private settingsService: SettingsService,private userService:UserService,
      private socialAuthService: AuthService,private modalService: NgbModal,
      private commonService:CommonService) { 
    this.appUrl = environment.appUrl?environment.appUrl:"";
  }

  private getCompanies(): void {
    this.loaded = false;
    this.settingsService.getCompanies().then(response => {
      this.companies = response;
    })
    .catch(err=>{
      this.loaded = true;
    });   
  }

  public removeUserFromCompany(userId){
    this.settingsService.removeUserFromCompany(this.companyId,userId).then(response =>{
      this.getAllUserForCompany()
    }).catch(err=>{
      console.log('');
    });
  }

  public addUserToCompany(userId){
    this.settingsService.addUserToCompany(this.companyId,userId).then(response =>{
      this.getAllUserForCompany()
    }).catch(err=>{
      console.log('');
    });
  }

  private setAllUser(){
    this.userService.getAllUsers().then(response => {
      this.allUser = response;
    }).catch(err=>{
      this.loaded = true;
    });   
  }

  public updateAvailableUserForCompany(){
    this.availableUser = (this.allUser || []).filter((user)=>{
      return this.allUserIds.indexOf(user.userId) === -1;
    })
  }

  public getAllUserForCompany(){ 
    this.allUserIds = [];
    this.settingsService.getAllUserForCompany(this.companyId).then(users=>{
      this.selectedCompanyUser = users;
      users.forEach(user => {
        this.allUserIds.push(user.userId);
      });
      this.updateAvailableUserForCompany();
    });
  }

  ngOnInit() {
    this.getCompanies();
    this.setAllUser();    
  }

  public saveUser(){
    this.newUser.companyId = this.companyId; 
    console.log(this.newUser);
  }

  removeUserFromCompanyOnConfirmation(userId) {
    this.commonService.askConfirmation("User will be removed from assigned activities for selected companies and will be remove from company!\nDo you want to continue?",
    "Remove user","Remove","Cancel",
    ()=>{
      this.removeUserFromCompany(userId);
    },()=>{});
  }

  addUserToCompanyOnConfirmation(userId) {
    this.commonService.askConfirmation("Do you want to add user to selected company?!\nDo you want to continue?",
    "Add user","Add","Cancel",
    ()=>{
      this.addUserToCompany(userId);
    },()=>{});
  }

}



