import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule }    from '@angular/common/http';
import { AppComponent } from './app.component';
import { LoginLoginComponent } from './login/app.login.login.component';
import { LogedInComponent } from './loggedin/app.logedin.component';
import { RegisterComponent } from './register/app.register.component';
import { ComboboxComponent } from './customComponents/combobox/app.combobox.component';
import { LoginForgetComponent } from './login/app.login.forget.component';
import { NotActiveComponent } from './notactive/notactive.component';
import { AccountActiveComponent } from './accountActive/accountactive.component';

import { RouterModule }   from '@angular/router';
import {  FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AngularFontAwesomeModule } from 'angular-font-awesome';
import { SidebarComponent } from './sidebar/sidebar.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CalendarModule } from 'angular-calendar';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
import { DemoUtilsModule } from './demo-utils/module';
import { ComplienceCalendarComponent } from './complience-calendar/complience-calendar.component';
import { EditUser } from './user/edit/app.edit.component';
import { ComplianceDashboardComponent } from './compliance-dashboard/compliance-dashboard.component';
import { ChartsModule } from 'ng2-charts';
import { ComplianceReportComponent } from './compliance-report/compliance-report.component';
import { ComplianceActivitiesComponent } from './compliance-activities/compliance-activities.component';
import { FilterPipe} from './filters/filter.pipe';
import { ShowErrorsComponent } from './customComponents/error/show-errors.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { DataTablesModule } from 'angular-datatables';
import { ComplianceRepositoryComponent } from './compliance-repository/compliance-repository.component';
import { SettingsComponent } from './settings/settings.component';
import { MasterActivitiesComponent } from './settings/masteractivities.component';
import { BlankComponentComponent } from './blank-component/blank-component.component';
import { SocialLoginModule, AuthServiceConfig } from "angular5-social-login";
import { getAuthServiceConfigs } from "./service/socialloginConfig";
import { AddUserComponent } from './settings/add-user/add-user.component';
import { MasterActivityUpdateComponent } from './settings/master-activity-update/master-activity-update.component';
import { AddCompanyComponent } from './settings/add-company/add-company.component';
import { ConfirmationBoxComponent } from './customComponents/confirmation-box/confirmation-box.component';
import { AgGridModule } from 'ag-grid-angular';
import { NewActivitiesComponent } from './settings/new-activities/new-activities.component';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { NewUserComponent } from './settings/new-user/new-user.component';

@NgModule({
  declarations: [
    AppComponent,ShowErrorsComponent,LoginLoginComponent,RegisterComponent,LoginForgetComponent,LogedInComponent, 
		SidebarComponent, ComplienceCalendarComponent, ComplianceDashboardComponent, ComplianceReportComponent, 
		ComplianceActivitiesComponent,FilterPipe,ComboboxComponent,NotActiveComponent,AccountActiveComponent, ComplianceRepositoryComponent, BlankComponentComponent,SettingsComponent,
		EditUser,MasterActivitiesComponent, AddUserComponent, MasterActivityUpdateComponent, AddCompanyComponent, ConfirmationBoxComponent, NewActivitiesComponent, NewUserComponent
  ],
  imports: [
		FormsModule,
		ReactiveFormsModule,
		AngularFontAwesomeModule,
		BrowserModule,
		DataTablesModule,
		HttpClientModule,
		BrowserAnimationsModule,
		CalendarModule.forRoot(),
		NgbModalModule.forRoot(),
		ChartsModule,
		DemoUtilsModule,
		NgbModule.forRoot(),
		SocialLoginModule,
		AgGridModule.withComponents([]),
		NgMultiSelectDropDownModule.forRoot(),
		RouterModule.forRoot([
		  {
			path: '',
			component: LoginLoginComponent
		  },
		  {
			path: 'loggedin',
			component: LogedInComponent
		  },
		  {
			path: 'forget',
			component: LoginForgetComponent
			},
			{
				path: 'editProfile',
				component: EditUser
				},
		  {
		  	path: 'dashboard',
		  	component: ComplianceDashboardComponent,
		  },
		  {
		  	path: 'activities',
		  	component: ComplienceCalendarComponent
		  },
		  {
		  	path: 'report',
		  	component: ComplianceReportComponent
		  },
		  {	
			path: 'register',
			component: RegisterComponent
		  },
		  {	
			path: 'notactive',
			component: NotActiveComponent
		  },
		  {	
			path: 'activated',
			component: AccountActiveComponent
		  },
		  {
		  	path: 'repository',
		  	component: ComplianceRepositoryComponent,
		  	data : {repository_type : 'Complied- In time'}
		  },
		  {
		  	path: 'repositorydelayed',
		  	component: ComplianceRepositoryComponent,
		  	data : {repository_type : 'Complied-Delayed'}
		  },{
		  	path : 'blank',
		  	component : BlankComponentComponent
		  },{
		  	path : 'settings',
		  	component : SettingsComponent
			},
			{
		  	path : 'masteractivity',
		  	component : MasterActivitiesComponent
			}

		],{useHash: true})
	
	],
  providers: [{
		provide: AuthServiceConfig,
		useFactory: getAuthServiceConfigs
	},ConfirmationBoxComponent],
  bootstrap: [AppComponent],
	exports: [ComplienceCalendarComponent],
	entryComponents:[ConfirmationBoxComponent]
})
export class AppModule {}



