import { Injectable } from '@angular/core';
import {CompanyBean} from '../typeBean/CompanyBean';
import {CompanyLocationBean} from '../typeBean/CompanyLocationBean';
import { HttpHeaders,HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import {BaseService} from './BaseService';
import { LoginService } from '../service/loginService';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class CompanyService  extends BaseService{
  private appUrl;
  constructor(private http: HttpClient,private loginService: LoginService) { 
    super();
		this.appUrl = environment.appUrl?environment.appUrl:"";
  }

  public addCompany(company:CompanyBean){
    let headers = this.loginService.getHeaders();
		let url = this.appUrl+'/rest/company/addcompany';
		return this.http.post(url,company,headers).toPromise().then(response => response).catch(this.handleError);
  }

  public addCompanyLocation(companyLocations:CompanyLocationBean){
    let headers = this.loginService.getHeaders();
		let url = this.appUrl+'/rest/location/addCompanylocation';
		return this.http.post(url,companyLocations,headers).toPromise().then(response => response).catch(this.handleError);
  }

  public getAllCompanies(){
    let headers = this.loginService.getHeaders();
    let url = this.appUrl+'/rest/settings/companies';
    return this.http.get(url,headers).toPromise().then(response=>response).catch(this.handleError);
  }

  public getCompanyLocation(companyId:string):Promise<any>{
    let headers = this.loginService.getHeaders();
    let url = this.appUrl+'/rest/location/companylocations/'+companyId;
    return this.http.get(url,headers).toPromise().then(response=>response).catch(this.handleError);
  }

  public updateCompanyDetails(name:string,abbr:string,id:string){
    let headers = this.loginService.getHeaders();
    let url = this.appUrl+'/rest/company/updateCompanyDetails';
		return this.http.post(url,{name,abbr,id},headers).toPromise().then(response => response).catch(this.handleError);
  }
}



