import { Component, OnInit } from '@angular/core';
import { UserService } from '../../service/user.service';
import { CommonService } from '../../service/commonService';
@Component({
  selector: 'NewUser',
  templateUrl: './new-user.component.html',
  styleUrls: ['./new-user.component.css']
})
export class NewUserComponent implements OnInit {

  public dropdownSettings;
  public selectedUser={};
  constructor(private userService:UserService,private commonService:CommonService) {
    this.dropdownSettings = {
      singleSelection: true,
      idField: 'userId',
      textField: 'firstName',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      allowSearchFilter: true,
      enableCheckAll:false      
    };
   }
   //@ts-ignore
  public newUser = {companyId:"",firstName:"",lastName:"",email:"",pass:"",phone:"",userTypeId:"",fullUser:"",userId:""};
  public users

  public loadUser(userId){
    console.log(this.newUser);
    var selectedUser = this.users.filter((user)=>{
        if(user.userId == userId){
          return user;
        }
    });
    this.selectedUser = selectedUser[0];      
  }

  public saveUser(){
    console.log(this.newUser)
    this.userService.createUser(this.newUser).then(()=>{
      this.commonService.showMessage("User updation completed","Success")
    });
  }

  public getUsers(){
    this.userService.getAllUsers().then(users=>{
      this.users = users;
    });
  }

  public onItemSelect(evt){
    this.loadUser(evt.userId);
  }

  public updateUser(){
    this.userService.updateUser(this.selectedUser).then(()=>{this.commonService.showMessage("User updation completed","Success")});
  }

  ngOnInit() {
    this.getUsers()
  }

}



