import { Component, OnInit } from '@angular/core';
import {CompanyService} from '../../service/company.service';
import {LocationService} from '../../service/locationService';
import {CompanyLocationBean} from '../../typeBean/CompanyLocationBean';
import {CompanyBean} from '../../typeBean/CompanyBean';
import { CommonService } from '../../service/commonService';
@Component({
  selector: 'AddCompany',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css']
})
export class AddCompanyComponent implements OnInit {

  constructor(private companyService:CompanyService,private locationService:LocationService,private commonService:CommonService) { }
  public company = {companyName:"",abbr:""};
  public allRegisteredCompanies = [];
  public companyLocations = [];
  public locationsForCompany = [];
  public companyLocationsToAdd = [];
  public selectedCompanyId; 
  public companyLocationBean:CompanyLocationBean;
  public selectedCompany = {};
  public updateCompanyData = {"name":undefined,"abbriviation":undefined};
  
  //This is to store just locations 
  public currentLocationIds = [];

  addCompany():void{
    console.log(this.company);
    this.companyService.addCompany(<CompanyBean>this.company).then(()=>{this.loadCompanies();});
  }

  getCompanyLocation(){
    this.companyService.getCompanyLocation(this.selectedCompanyId).then((location)=>{ 
      this.locationsForCompany = location;
      this.setCurrentLocationIds(location);
      this.setAvailableLocations();
    });

    this.setSelectedCompany();
  }

  public setSelectedCompany(){
    this.updateCompanyData = this.allRegisteredCompanies.filter((company)=>{
      if(company.companyId == this.selectedCompanyId){
        return company;
      }      
    })[0];
  }

  setAvailableLocations(){
    this.companyLocationsToAdd = [];
    this.locationService.getAllLocationFromDb().then(locs=>{
      locs.forEach((loc)=>{
        if(this.currentLocationIds.indexOf(loc.locationId) === -1){
          this.companyLocationsToAdd.push(loc);
        }      
      });
    });
  }

  updateCompanyDetails(){
    this.companyService.updateCompanyDetails(this.updateCompanyData.name,this.updateCompanyData.abbriviation,this.selectedCompanyId).then(()=>{
      this.commonService.showMessage("Company details updated","Success");
    });
  }

  setCurrentLocationIds(locations){
    this.currentLocationIds = [];
    locations.forEach(element => {
      this.currentLocationIds.push(element.locationId);
    });
    console.log(this.currentLocationIds);
  }

  addLocationToCompany(locationId:string){
    var locationIds = [locationId];
    var locationParam = {companyId:this.selectedCompanyId,locationIds:locationIds};
    this.companyService.addCompanyLocation(locationParam);
    this.getCompanyLocation();
  }

  loadCompanies(){
    this.companyService.getAllCompanies().then((allCompanies)=>{
      this.allRegisteredCompanies = allCompanies;
    });
  }

  ngOnInit() { 
    this.loadCompanies();
  }

}



