import { Injectable } from '@angular/core';
import {ConfirmationBoxComponent} from '../customComponents/confirmation-box/confirmation-box.component';
import 'rxjs/add/operator/toPromise';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
@Injectable()
export class CommonService{
	constructor(private modalService: NgbModal) {
		
	}

	public askConfirmation(confirmationMessage:string,header,okTxt,cancelTxt,ok,cancel){
		const modalRef = this.modalService.open(ConfirmationBoxComponent);
		modalRef.result.then(result=>{
			if(ok){ok()}
		},reson=>{
			if(cancel){cancel()}
		});
		modalRef.componentInstance.message = confirmationMessage;
		modalRef.componentInstance.okBtn = okTxt?okTxt:"Ok";
		modalRef.componentInstance.cancelBtn = cancelTxt?cancelTxt:"Cancel";
		modalRef.componentInstance.header = header?header:"Confirm";
	}

	public showMessage(message:string,header){
		const modalRef = this.modalService.open(ConfirmationBoxComponent);
		modalRef.componentInstance.message = message;
		modalRef.componentInstance.okBtn = "Ok";
		modalRef.componentInstance.header = header?header:"Confirm";
	}
	
	
}


