import { Injectable } from '@angular/core';
import {CompanyBean} from '../typeBean/CompanyBean';
import {CompanyLocationBean} from '../typeBean/CompanyLocationBean';
import { HttpHeaders,HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import {BaseService} from './BaseService';
import { LoginService } from '../service/loginService';
import 'rxjs/add/operator/toPromise';
@Injectable()
export class UserService   extends BaseService{
  private appUrl;
  constructor(private http: HttpClient,private loginService: LoginService) {
    super();
		this.appUrl = environment.appUrl?environment.appUrl:"";
   }

  getAllUsers(){
    let headers = this.loginService.getHeaders();
    let url = this.appUrl+'/rest/user/allUser';
    return this.http.get(url,headers).toPromise().then(response=>response).catch(this.handleError);
  }

  updateUser(userData){
    let headers = this.loginService.getHeaders();
    let url = this.appUrl+'/rest/user/updateUserForMaster';
    return this.http.post(url,userData,headers).toPromise().then(response=>response).catch(this.handleError);
  }

  createUser(userData){
    let headers = this.loginService.getHeaders();
    let url = this.appUrl+'/rest/user/registeruserformaster/comp';
    return this.http.post(url,{"userBean":userData},headers).toPromise().then(response=>response).catch(this.handleError);
  }
}



