import { Injectable } from '@angular/core';
import { RasPiDetails } from '../rasPiDetails';
import { ComData } from '../comData';
import { Headers, Http,RequestOptions } from '@angular/http';
import { HttpHeaders,HttpClient } from '@angular/common/http';
import { LoginBean } from '../typeBean/loginBean';
import { LoginService } from './loginService';
import { environment } from '../../environments/environment';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class SettingsService {
	constructor(private http: HttpClient, private loginService: LoginService) {
		this.appUrl = environment.appUrl?environment.appUrl:"";
	}
	appUrl = "";
	loginBean :LoginBean = new LoginBean();
	
	private handleError(error: any): Promise<any> {
	  console.error('An error occurred', error); // for demo purposes only
	  return Promise.reject(error.message || error);
	}

	public removeUserFromCompany(companyId,userId){
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/settings/removeUserFromCompany` ;
		return this.http.post(url,{companyId,userId},headers).toPromise().then(response => response).catch(this.handleError);
	}

	public addUserToCompany(companyId,userId){
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/settings/addUserToCompany` ;
		return this.http.post(url,{companyId,userId},headers).toPromise().then(response => response).catch(this.handleError);
	}

	public addUser(userBean:any){
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/user/registeruser` ;
		return this.http.post(url,userBean,headers).toPromise().then(response => response).catch(this.handleError);
	}

	getAllUserForCompany(companyId):Promise<any>{
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/user/allUserForCompany/`+companyId ;
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}

	addUserForActivity(userId,activityIds):Promise<any>{
		let headers = this.loginService.getHeaders();
		var settings = [];
		for(var i =0;i<activityIds.length;i++){
			settings.push({userId,activityId:activityIds[i]});
		}
		const url = this.appUrl+`/rest/settings/setUserForActivity` ;
		return this.http.post(url,settings,headers).toPromise().then(response => response).catch(this.handleError);
	}

	setDateForUser(newDate,activityIds):Promise<any>{
		let headers = this.loginService.getHeaders();
		var changeDateBean = {date:newDate,activityId:activityIds};
		const url = this.appUrl+`/rest/settings/changeDate` ;
		return this.http.post(url,changeDateBean,headers).toPromise().then(response => response).catch(this.handleError);
	}

	removeUserForActivity(userId,activityId):Promise<any>{
		let headers = this.loginService.getHeaders();
		var settings = {userId,activityId};
		const url = this.appUrl+`/rest/settings/removeUserForActivity` ;
		return this.http.post(url,settings,headers).toPromise().then(response => response).catch(this.handleError);
	}

	getSettings():Promise<any>{
		let headers = this.loginService.getHeaders();
		let companyId = this.loginService.getCompany();
		const url = this.appUrl+`/rest/settings` ;
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}

	setSetting(settings:any):Promise<any>{
		const url = this.appUrl+`/rest/settings` ;
		return this.http.post(url,settings).toPromise().then(response => response).catch(this.handleError);
	}
	
	sendAlertsNow(){
		const url = this.appUrl+`/rest/alerts/sendNow` ;
		return this.http.post(url,{}).toPromise().then(response => response).catch(this.handleError);
	}

	getCompanies():Promise<any>{
		let headers = this.loginService.getHeaders();
		let companyId = this.loginService.getCompany();
		const url = this.appUrl+`/rest/settings/companies` ;
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}

	getAllCompaniesActivity(companyId:string,year:string,month:string):Promise<any>{
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/settings/getAllActivities/`+companyId+"/"+year+"/"+month ;
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}

	getUserForActivity(activityId:string):Promise<any>{
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/settings/getUserForActivity/`+activityId ;
		return this.http.get(url,headers).toPromise().then(response => response).catch(this.handleError);
	}

	uploadFile(file,companyId,callback){
		const httpOptions = {
			headers: new HttpHeaders({
			  'Accept-Encoding':  'multipart/form-data',
			  'auth': sessionStorage.getItem("auth")
			})
		  };
		
		const url = this.appUrl+`/rest/settings/upload/`+companyId;
		let formData = new FormData();
		formData.append("uploadedFile",file);
		return this.http.post(url,formData,httpOptions).toPromise().then((response)=>{callback(response);}).catch(this.handleError);
	}

	public setActivityUser(activityUser,activityIds:any[]){
		let headers = this.loginService.getHeaders();
		const url = this.appUrl+`/rest/activity/setActivityUser` ;
		return this.http.post(url,{activityUser,activityIds},headers).toPromise().then(response => response).catch(this.handleError);
	}
	
}


