import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { SettingsService } from '../../service/SettingsService';
import {SettingsBean } from '../../typeBean/SettingsBean';
import { SocialLoginModule, AuthServiceConfig, GoogleLoginProvider,AuthService } from "angular5-social-login";
import { environment } from '../../../environments/environment';
import { NgbModalModule,NgbModal,  ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'MasterActivitiesUpdate',
  templateUrl: './master-activity-update.component.html',
  styleUrls: ['./master-activity-update.component.css']
})
export class MasterActivityUpdateComponent implements OnInit {

  constructor(private settingsService: SettingsService,private socialAuthService: AuthService, private modalService: NgbModal) { 
    this.appUrl = environment.appUrl?environment.appUrl:"";
  }

  appUrl = "";
public loaded:boolean = false;
public companies = null;
public months:Array<string> = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
public companyId:string = "all";
public year:string = "all";
public month:string = "all";
public activities:any = [];
public activityUser:any = [];
private dataTable;
public newDate;
public allUserForCompany = [];
public id;
  public addUser = false;
  public activitiesLoading = false;
  public enabledDiv = "";
  private closeResult: string;
  private popupMsg: string;

/* Add user values */
public firstname;
public lastname;
public email;
public password;
public phone;
public usertype;
public activityUserText;
  public newUser = { companyId: "" };  
  @ViewChild('showMessage') showMessage: ElementRef;
  
  
public setActivityUser(user,activityId){
  var ids = this.getAllSelectedIds(); 
  this.settingsService.setActivityUser(this.activityUserText,ids);
}  
private getCompanies(): void {
  this.loaded = false;
  this.settingsService.getCompanies().then(response => {
    this.companies = response;
  })
  .catch(err=>{
    this.loaded = true;
  });   
}

public showAddUser(show){
  this.addUser = show;
  this.enabledDiv = "addUser";
}

public saveUser(){
  this.newUser.companyId = this.companyId;
  console.log(this.newUser);
}

public setNewDate():void{
  var ids = this.getAllSelectedIds();
  console.log(ids);
  if(this.newDate){
    this.settingsService.setDateForUser(this.newDate,ids).then(response => {
      // if (response) {
        this.openModal("New date is been set for the activity successfully")
      // }
    })
    .catch(err=>{
      this.loaded = true;
    });
  }
}

private openModal(Message:any) {
  this.popupMsg = Message;
  this.modalService.open(this.showMessage, { size: 'lg', backdrop: 'static' }).result.then((result) => {
    this.closeResult = `Closed with: ${result}`;
  }, (reason) => {
    this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
  });
}

private getDismissReason(reason: any): string {
  if (reason === ModalDismissReasons.ESC) {
    return 'by pressing ESC';
  } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
    return 'by clicking on a backdrop';
  } else {
    return  `with: ${reason}`;
  }
}

private initDataTable(dataSet){
  let that = this;
  this.dataTable = $('.table').DataTable( {
    data: dataSet,
    dom: '<"top"i>rt<"bottom"flp><"clear">',
    columns: [
      // @ts-ignore
        { title: "Id" ,mDataProp :"activityId"},
        // @ts-ignore
        { title: "Company Name" ,mDataProp :"name"},
        // @ts-ignore
        { title: "Name" ,mDataProp :"activityName"},
        // @ts-ignore
        { title: "Location" ,mDataProp :"locationName"},
        // @ts-ignore
        { title: "Law description" ,mDataProp :"lawDesc"},
        // @ts-ignore
        { title: "Due date" ,mDataProp :"duedate"}
        
    ]
} );

$('.table tbody').on( 'click', 'tr', function (e) {
  if ( $(this).hasClass('selected') ) {
      $(this).removeClass('selected');
  }
  else {
      if(!e.ctrlKey){
        that.dataTable.$('tr.selected').removeClass('selected');
      }      
      $(this).addClass('selected');
      that.id = $(this).find("td:eq(0)").text().trim();
      if(!e.ctrlKey){
        var dataForRow = that.dataTable.row(this).data();
        that.getUserForActivity(that.id,dataForRow.companyId);
        console.log({dataForRow});
      }
  }
} );
}

public getAllSelectedIds(){
  var selectedRow = this.dataTable.$('tr.selected');
  var ids = [];
  for(var i=0;i<selectedRow.length;i++){
    var id = $(selectedRow[i]).find("td:eq(0)").text().trim();
    ids.push(id);
  }
  return ids;
}

public getAllCompaniesActivity(): void {
  this.addUser = false;
  this.enabledDiv = "activities";
  this.activitiesLoading = true;
  if(this.dataTable!=undefined){
    this.dataTable.destroy();
  }
  this.settingsService.getAllCompaniesActivity(this.companyId,this.year,this.month).then(response => {
    this.activitiesLoading = false;
    this.activities = response;  

    this.initDataTable(response); 
  })
  .catch(err=>{
    this.activitiesLoading = false;
    this.loaded = true;
  });   
}

public getUserForActivity(activityId,companyId): void {
  this.getAllUserForCompany(companyId); 
  this.settingsService.getUserForActivity(activityId).then(response => {
    this.activityUser = response;    
  })
  .catch(err=>{
    this.loaded = true;
  });   
}

 private getAllUserForCompany(companyId){
  companyId = companyId || this.companyId;
  this.settingsService.getAllUserForCompany(companyId).then(response => {
    this.allUserForCompany = response;    
  })
  .catch(err=>{
    this.loaded = true;
  });
 }

 public addUserForActivity(userId,companyId){
    var ids = this.getAllSelectedIds();
    this.settingsService.addUserForActivity(userId,ids).then(response => {
      // this.allUserForCompany = response; 
      this.openModal("User added successfully to the activity.")
      if(!this.id){
        this.getUserForActivity(this.id,companyId);   
      }
      
    })
    .catch(err=>{
      this.loaded = true;
    });
 }

 public removeUserForActivity(userId,companyId){
  this.settingsService.removeUserForActivity(userId,this.id).then(response => {
    this.getUserForActivity(this.id,companyId);
  })
  .catch(err=>{
    this.loaded = true;
  });
}



  ngOnInit() {
   this.getCompanies();     
  
  }
}


