import { Component, OnInit,ViewChild } from '@angular/core';
import { AgGridAngular } from 'ag-grid-angular';
import {LocationService} from '../../service/locationService'
import {CompanyService} from '../../service/company.service'
import {ActivityService} from '../../service/activityService'
import {SettingsService} from '../../service/SettingsService'
import { CommonService } from '../../service/commonService';

@Component({
  selector: 'NewActivities',
  templateUrl: './new-activities.component.html',
  styleUrls: ['./new-activities.component.css']
})
export class NewActivitiesComponent implements OnInit {
  //MultiselectDropDown : https://www.npmjs.com/package/ng-multiselect-dropdown
  constructor(private locationService:LocationService,private companyService:CompanyService,
      private activityService:ActivityService,private settingsService: SettingsService,private cService:CommonService) { 
    this.components = { datePicker: getDatePicker() };
  }
 
  @ViewChild('agGrid') agGrid: AgGridAngular;

  components ;
  locations = [];
  columnDefs = [
    {headerName: 'Location', field: 'location',sortable: true, filter: true,editable: true, cellEditor: 'agSelectCellEditor',
    width:100,cellEditorParams: {
          values:this.locations}},
    {headerName: 'Comp Area', field: 'compArea',sortable: true, filter: true,editable: true,cellEditor: 'agSelectCellEditor',
    width:100,cellEditorParams: {values: ["Indirect Tax","Direct Tax","Labour Law","Corporate Law"]}},
    {headerName: 'Activity name', field: 'activityName',sortable: true, filter: true,editable: true},
    {headerName: 'Description', field: 'description',sortable: true, filter: true,editable: true,cellEditor: 'agLargeTextCellEditor'},
    {headerName: 'Law desc', field: 'lawDescription',sortable: true, filter: true,editable: true,cellEditor: 'agLargeTextCellEditor'},
    {headerName: 'Risk', field: 'risk',sortable: true, filter: true,editable: true,cellEditor: 'agSelectCellEditor',
    width:80,cellEditorParams: {values: ["High","Medium","Low"]}},
    {headerName: 'Consequence', field: 'consequence',sortable: true, filter: true,editable: true,cellEditor: 'agLargeTextCellEditor'},
    {headerName: 'Form', field: 'form',sortable: true, filter: true,editable: true,cellEditor: 'agLargeTextCellEditor'},
    {headerName: 'Periodicity', field: 'periodicity',sortable: true, filter: true,editable: true,width:100,cellEditor: 'datePicker'},
    {headerName: 'Per desc', field: 'periodicityDesc',sortable: true, filter: true,editable: true,cellEditor: 'agLargeTextCellEditor'},
];

companyList = [];
selectedItems = [];
dropdownSettings = {};
rowData = [{}];
filesToUpload;

/////////////////////////////////////////
public months:Array<string> = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
public companyId:string;
public addedFiles;

public uploadActivities(){
  this.settingsService.uploadFile(this.filesToUpload,this.companyId,(resp)=>{this.cService.showMessage("Activities are added succefully","Success");});
}

public setFiles(event){
  for(let files of event.target.files){
    this.filesToUpload = files;
  }
}

public addRow(){
  this.agGrid.gridOptions.rowData.push({})
  this.agGrid.api.setRowData(this.agGrid.gridOptions.rowData);  
}

public saveActivities(){
  console.log(this.agGrid.gridOptions.rowData,this.selectedItems)
  var companies = this.selectedItems.map((company)=>(company.companyId))
  this.activityService.saveActivitiesForCompanies(companies,this.agGrid.gridOptions.rowData).then(()=>{
    this.cService.showMessage("Activities are added succefully","Success");
  }).catch((err)=>{this.cService.showMessage(JSON.stringify(err),"Error");})
}

private setLocation(){  
  this.locationService.getAllLocationFromDb().then((locationsData)=>{
    this.locations = locationsData.map(loc => {
      return loc.locationName
    });        

    this.columnDefs[0].cellEditorParams.values = this.locations
  })
}

private getAllCompanies(){
  this.companyService.getAllCompanies().then(companies=>{
    this.companyList = companies;
  });
}

onItemSelect(item: any) {
  console.log(item);
}
onSelectAll(items: any) {
  console.log(items);
}

  ngOnInit() {
    this.setLocation();
    this.getAllCompanies();
this.dropdownSettings = {
      singleSelection: false,
      idField: 'companyId',
      textField: 'name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
  }

}

function getDatePicker() {
  function Datepicker() {}
  Datepicker.prototype.init = function(params) {
    this.eInput = document.createElement("input");
    this.eInput.value = params.value;
    this.eInput.type = 'date';
    //@ts-ignore
    //$(this.eInput).datepicker({ dateFormat: "mm/dd/yy" });
  };
  Datepicker.prototype.getGui = function() {
    return this.eInput;
  };
  Datepicker.prototype.afterGuiAttached = function() {
    this.eInput.focus();
    this.eInput.select();
  };
  Datepicker.prototype.getValue = function() {
    return this.eInput.value;
  };
  Datepicker.prototype.destroy = function() {};
  Datepicker.prototype.isPopup = function() {
    return false;
  };
  return Datepicker;
}


